@charset "UTF-8";
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Regular"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Medium"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Semi-Bold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Bold"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit; }

img {
  max-width: 100%;
  height: auto; }

.page {
  height: 100%; }

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Helvetica", "Arial", sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  color: #2c2e3f;
  text-align: left;
  background-color: #ffffff; }

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px; }

.heading {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 55px;
  font-weight: 600;
  line-height: 80px;
  letter-spacing: -1.25px;
  color: #141517; }
  .heading::after {
    position: relative;
    display: block;
    bottom: -20px;
    left: 0;
    content: "";
    width: 230px;
    height: 4px;
    background-color: #fe7865; }

.button {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 30px 40px;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 2px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: #fe7865;
  border: 2px solid #fe7865; }
  .button:hover, .button:focus {
    background-color: #f17d4b; }
  .button:active {
    opacity: 0.8; }
  .button:disabled {
    opacity: 0.8; }
  @media (max-width: 767px) {
    .button {
      width: 288px; } }

.link {
  color: #fe7865; }
  .link:hover, .link:focus {
    color: #f17d4b; }

.link--tel {
  text-decoration: underline; }
  .link--tel:active {
    text-decoration: none; }

.nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  color: #141517; }

.nav__logo {
  margin-right: auto; }

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 40px 0;
  justify-content: center;
  align-items: center; }

.nav__list-item:not(:last-child) {
  margin-right: 40px; }

.nav__link:hover, .nav__link:focus {
  color: #fe7865; }

.nav__link:active {
  color: #f17d4b; }

.nav__link--tel {
  margin-left: 70px;
  padding-left: 36px;
  text-decoration: underline; }
  .nav__link--tel:active {
    text-decoration: none; }

.nav__button {
  display: none; }

.hero {
  min-height: 860px;
  color: #ffffff;
  background-color: rgba(44, 46, 63, 0.5);
  background: url(../../img/backgrounds/hero-bg.jpg) center/cover no-repeat; }

/*
