  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}
*/
.hero__container {
  padding: 185px 58px 250px 700px; }

.hero__heading {
  margin-bottom: 35px;
  letter-spacing: -1.11;
  color: #ffffff; }
  .hero__heading::after {
    display: none; }

.hero__text {
  max-width: 340px;
  margin-bottom: 40px;
  letter-spacing: -0.47px;
  line-height: 1.7; }

.hero__button {
  max-width: 340px;
  padding-right: 95px;
  justify-content: flex-start;
  font-size: 12px;
  text-align: left; }
  .hero__button::after {
    position: absolute;
    top: center;
    right: 40px;
    content: "";
    width: 14px;
    height: 14px;
    background: url("../../img/icons/arrow-down.svg") 100% 100% no-repeat; }

.destinations__heading {
  padding-top: 230px;
  max-width: 450px;
  margin-bottom: 70px; }

.destinations__intro {
  margin: 0;
  padding: 0;
  max-width: 350px;
  color: rgba(44, 46, 63, 0.7); }

.destinations__preview-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin-top: 55px;
  justify-content: center; }

.destination-card {
  position: relative;
  display: flex;
  margin-right: 60px;
  margin-bottom: 100px;
  width: 350px;
  min-height: 430px;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center; }
  .destination-card:nth-child(3n) {
    margin-right: 0; }

.destination-card__title {
  display: flex;
  flex-wrap: wrap;
  padding: 18px 60px;
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
  text-align: center;
  background-color: #ffffff; }

.destination-card__link {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(20, 21, 23, 0.6);
  opacity: 0; }
  .destination-card__link:hover, .destination-card__link:focus {
    opacity: 1; }
  .destination-card__link:active {
    opacity: 0.7; }

.destination-card__link-wrap {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 25px;
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.7); }

.destination-card__img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  object-fit: cover;
  overflow: hidden;
  z-index: -1; }

.countries {
  min-height: 1200px;
  background: url("../../img/backgrounds/europe-map.svg") center no-repeat; }

.countries__sliders {
  margin: 0;
  padding: 0;
  list-style: none; }

.country-tabs {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  max-width: 900px;
  border-bottom: 1px solid rgba(44, 46, 63, 0.3); }

.country-tabs__item:not(:last-child) {
  margin-right: 100px; }

.country-tabs__link {
  display: flex;
  flex-wrap: wrap;
  padding: 15px 0;
  font-size: 20px;
  line-height: 25px;
  letter-spacing: -0.41px;
  color: rgba(44, 46, 63, 0.3); }
  .country-tabs__link:hover, .country-tabs__link:focus {
    color: #fe7865; }
  .country-tabs__link:active {
    color: #f17d4b; }

.country {
  position: relative; }

/*
