.country__inner {
  display: grid;
  grid-template-areas: "heading image review" "text image review" "button image review";
  grid-template-columns: 2fr 2fr 230px;
  gap: 30px; }

.country__heading {
  grid-area: heading; }

.country__description {
  grid-area: text;
  margin-top: 60px;
  margin-bottom: 50px;
  color: rgba(44, 46, 63, 0.7); }

.country__button {
  width: 250px;
  grid-area: button;
  padding: 30px; }

.review {
  position: absolute;
  top: 50%;
  right: 0;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  padding: 30px;
  width: 382px;
  min-width: 367px;
  transform: translateY(-50%); }

.review__text {
  margin-top: 40px;
  margin-bottom: 35px;
  font-size: 14px;
  letter-spacing: -0.41px;
  color: rgba(44, 46, 63, 0.7); }

.review__author-wrapper {
  display: flex;
  flex-wrap: wrap; }

.review__author-img {
  display: block;
  width: 38px;
  height: 38px;
  margin-right: 16px;
  border-radius: 50%;
  background-color: rgba(20, 21, 23, 0.6); }

.review__author-name {
  font-weight: 500;
  font-style: normal; }
